/* title */
export function setServerColumns({
  handleEdit, handleDelete, type
}) {
  return [
    {
      title: '',
      dataIndex: 'deviceType',
      key: 'deviceType'
    },
    {
      title: type === 'repair' ? '' : '',
      dataIndex: 'contentType',
      key: 'contentType',
      render: value =>
        value === 1 ? '' : value === 2 ? '' : ''
    },
    {
      title: type === 'repair' ? '' : '',
      dataIndex: 'content',
      key: 'content'
    },
    {
      title: '',
      dataIndex: 'createTime',
      key: 'createTime'
    },
    {
      title: '',
      dataIndex: 'creater',
      key: 'creater'
    },
    {
      title: '',
      dataIndex: 'maintainId',
      key: 'maintainId',
      align: 'right',
      render: value => {
        return (
          <span style={{ color: 'var(--PC)', cursor: 'pointer' }}>
            <span onClick={() => handleEdit(value)}></span>
            <Divider type='vertical' />
            <span onClick={() => handleDelete({ value, type: 'single' })}>
              
            </span>
          </span>
        );
      }
    }
  ];
}
