/* form-columns */
export function setFormColumns({
  type, status, currentMain, handleRadio, contentType, onAddItem, onDelItem, handleDataValue, minValue, maxValue
}) {
  return [
    {
      key: 'content',
      label: type === 'repair' ? '' : '',
      value: <Input placeholder='' style={{ width: '330px' }} />,
      getFieldDecorator: {
        initialValue: status === 'edit' ? currentMain.content : undefined,
        rules: [
          {
            required: true,
            message: ''
          }
        ]
      }
    },
    {
      key: 'contentType',
      label: type === 'repair' ? '' : '',
      value: (
        <Radio.Group onChange={handleRadio}>
          <Radio value={1}></Radio>
          <Radio value={2}></Radio>
          <Radio value={3}></Radio>
        </Radio.Group>
      ),
      getFieldDecorator: {
        initialValue: status === 'edit' ? currentMain.contentType : 1,
        rules: [
          {
            required: true,
            message: ''
          }
        ]
      }
    },
    {
      key: 'extendBtn',
      label: <div />,
      displayNone: contentType === 1,
      colon: false,
      value: (
        <div>
          {contentType === 2 ? (
            [
              <Button type='primary' onClick={onAddItem}>
                
              </Button>,
              <Button style={{ marginLeft: '5px' }} onClick={onDelItem}>
                
              </Button>
            ]
          ) : (
              <div>
                <span></span>
                <div>
                  
                <Input
                    style={{ width: '200px' }}
                    value={minValue}
                    onChange={(e) => handleDataValue('minValue', e)}
                  />
                </div>
                <div>
                  
                <Input
                    style={{ width: '200px' }}
                    value={maxValue}
                    onChange={(e) => handleDataValue('maxValue', e)}
                  />
                </div>
              </div>
            )}
        </div>
      )
    }
  ];
}

