/* -columns */
export function setPlanModalColumns({
  status, currentMain, deviceByIdList, schemaByIdList
}) {
  return [
    {
      key: 'planName',
      label: '',
      value: <Input placeholder='' style={{ width: '250px' }} />,
      getFieldDecorator: {
        initialValue: status === 'edit' ? currentMain.planName : undefined,
        rules: [
          {
            required: true,
            message: ''
          }
        ]
      }
    },
    {
      key: 'deviceId',
      label: '',
      value: (
        <Select
          showSearch allowClear
          style={{ width: 250, }}
          placeholder=''
        >
          {
            deviceByIdList && deviceByIdList.map(item => {
              return <Option key={item.id} value={item.id}>
                {item.devName}
              </Option>
            })
          }
        </Select>
      ),
      getFieldDecorator: {
        initialValue: status === 'edit' ? Number(currentMain.deviceId) : undefined,
        rules: [
          {
            required: true,
            message: ''
          }
        ]
      }
    },
    {
      key: 'schemaId',
      label: '',
      value: (
        <Select
          showSearch allowClear
          style={{ width: 250, }}
          placeholder=''
        >
          {
            schemaByIdList && schemaByIdList.map(item => {
              return <Option key={item.id} value={item.id}>
                {item.planName}
              </Option>
            })
          }
        </Select>
      ),
      getFieldDecorator: {
        initialValue: status === 'edit' ? currentMain.schemaId : undefined,
        rules: [
          {
            required: true,
            message: ''
          }
        ]
      }
    },
    {
      key: 'excuteTime',
      label: '',
      value: <DatePicker
        mode='time' showTime
        format="HH:mm:ss"
        locale={locale}
        placeholder=""
        style={{ width: '250px', display: 'inline-block' }}
      />,
      getFieldDecorator: {
        initialValue: status === 'edit' ? moment(currentMain.excuteTime) : undefined,
        rules: [
          {
            required: true,
            message: ''
          }
        ]
      }
    },
    {
      key: 'planWaitTime',
      label: '',
      value: <TimeInput />,
      getFieldDecorator: {
        initialValue: status === 'edit' ?
          { waitTime: currentMain.waitTime, excuteUnit: currentMain.excuteUnit } : undefined,
        rules: [
          {
            required: true,
            message: '0'
          }
        ]
      }
    },
    {
      key: 'excuteCycle',
      label: '',
      value: <Select
        showSearch allowClear
        style={{ width: 250, }}
        placeholder=''
      >
        <Option value={1}></Option>
        <Option value={2}></Option>
      </Select>,
      getFieldDecorator: {
        initialValue: status === 'edit' ? currentMain.excuteCycle : 1,
        rules: [
          {
            required: true,
            message: ''
          }
        ]
      }
    },
  ]
}

