/*  columns */
export function setModalOperateColumns({
  type, onRadioChange, onCheckBoxChange, onInputNumber, onDescription, contents
}) {
  return [
    {
      title: type === 'repair' ? '' : '',
      dataIndex: 'content',
      key: 'content'
    },
    {
      title: type === 'repair' ? '' : '',
      dataIndex: 'contentType',
      key: 'contentType',
      render: value => value === 1 ? '' : value === 2 ? '' : ''
    },
    {
      title: type === 'repair' ? '' : '',
      dataIndex: 'contentType',
      key: 'result',
      render: (value, record) => {
        return <div>
          {
            value === 1 ? <Radio.Group
              onChange={(e) => onRadioChange(record, e.target.value)}
              defaultValue={1}
            >
              <Radio value={1}></Radio>
              <Radio value={2}></Radio>
            </Radio.Group> : value === 2 ? !isEmpty(contents) && <Checkbox.Group
              options={contents[record.maintainID].strList}
              onChange={(e) => onCheckBoxChange(record, e)}
            /> : value === 3 ? !isEmpty(contents) && <span>
              <span>{contents[record.maintainID].minValue}{' < '} </span>
              <InputNumber
                style={{ width: '80px' }}
                min={contents[record.maintainID].minValue}
                max={contents[record.maintainID].maxValue}
                // value = {}
                onChange={(e) => onInputNumber(record, e)}
              />
              <span>{' < '}{contents[record.maintainID].maxValue}</span>
            </span> : null
          }
        </div>
      }
    },
    {
      title: '',
      dataIndex: 'description',
      key: 'description',
      render: (value, record) => {
        return <Input onChange={(e) => onDescription(record, e.target.value)} />
      }
    },
  ]
}
